import {connect} from "react-redux";
import React, {Component} from "react";
import PropTypes from "prop-types";
import {getSelectedBillingAccountId} from "eshop/modules/auth/selectors/authorization";
import {isCartChangedInfoModalState} from "eshop/modules/checkout/selectors";
import {hideCartChangedInfoModal, showCartChangedInfoModal} from "eshop/modules/checkout/actions/app";
import {getCartWasChanged} from 'eshop/modules/cart/actions/cart';
import {OraButton} from "eshop/components/OraCommonComponents";

class CartChangedInfoModal extends Component {

    renderInfo() {
        return (
            <span dangerouslySetInnerHTML={{__html: this.props.text || this.props.defaultText}}/>
        );
    }

    getPrimaryButtonText() {
        return this.props.buttonText || this.props.defaultButtonText;
    }

    render() {
        return (
            <div>
                <div className="u-padding-all-m u-small-padding-s">
                    <div className="l-row u-spacing-top-m">
                        <div className="l-col l-col-12">
                            {this.renderInfo()}
                        </div>
                    </div>
                </div>
                <div className="l-row">
                    <div className="l-col l-col-small-12 l-col-medium-6 l-col-4 large-offset-by-8">
                        <OraButton onClick={this.props.buttonClick} className="u-w-100">
                            {this.getPrimaryButtonText()}
                        </OraButton>
                    </div>
                </div>
            </div>
        );
    }
}

CartChangedInfoModal.defaultProps = {
    defaultText: "<span>Ceny oferty zosta≈Çy zaktualizowane.</span>",
    defaultButtonText: "Zamknij"
};

CartChangedInfoModal.propTypes = {
    text: PropTypes.string,
    buttonText: PropTypes.string,
    buttonClick: PropTypes.func,
};

export default CartChangedInfoModal;